/*
Mixins
=========

This is a set of helper functions

*/
.rotateXY(@Xdegrees,@Ydegrees) {
  -webkit-transform: rotateX(@Xdegrees) rotateY(@Ydegrees);
      -ms-transform: rotateX(@Xdegrees) rotateY(@Ydegrees); // IE9+
          transform: rotateX(@Xdegrees) rotateY(@Ydegrees);
}
.transform-style(@type) {
  -webkit-transform-style: @type;
     -moz-transform-style: @type;
      -ms-transform-style: @type; // IE9+
          transform-style: @type;
}
.rotate3d(@x; @y; @z; @angle){
  -webkit-transform: rotate3d(@x,@y,@z,@angle);
  -moz-transform: rotate3d(@x,@y,@z,@angle);
  -ms-transform: rotate3d(@x,@y,@z,@angle);
  transform: rotate3d(@x,@y,@z,@angle);
}

/*
Font face
=========

You can use this function to quickly add a custom font, it will generate the correct syntax for you

```css
@font-face {
    .font-face(@fontname, @fontfile);
}
```

*/
.font-face(@fontname, @fontfile) {
    font-family: "@{fontname}";
    src: url("@{fontfile}.eot");
    src: url("@{fontfile}.eot?#iefix") format("embedded-opentype"),
         url("@{fontfile}.woff") format("woff"),
         url("@{fontfile}.ttf") format("truetype"),
         url("@{fontfile}.svg#@{fontname}") format("svg");
    font-weight: normal;
    font-style: normal;
}


