/*
stat-variant
=========
You can use this function to create a variation on the Stat(istic) element

```css
.my-stat {
    .stat-variant(@color);
}
```

*/
.stat-variant(@color) {
  > .stat-value{
    color:@color;
  }
}

/*
date-variant
=========

You can use this function to create a variation on the Date element

```css
.my-date {
    .date-variant(@color);
}
```

*/
.date-variant(@color) {
  > strong{
    background-color: @color;
    border-bottom:1px dashed darken(@color,12%);
    box-shadow: 0 1px 0 @color;
  }
  > em{
    color: @color;
  }
}

